@import (less) "./../less/color.less";
:host /deep/ .tabs-container {
    .tab-title {
        font-family: Arial;
        font-weight:  Bold;
        font-size: 0.75em !important;
    }
    i {
        font-size: 32px !important;
    }
    .tab-container {
        min-width: 184px !important;
        min-height: 5.375em !important;
        max-height: 5.375em !important;
        background-color: #EEEEEE !important;
        &.selected.enabledTab {
            background-color: white !important;
            border: .0625em solid transparent !important;
        }
        &.enabledTab {
            border: .0625em solid #eeeeee !important;
        }
        &.disabledTab {
            border: .0625em solid #eeeeee !important;
        }
    }
}

.sticky {
    top: 55px;
    z-index: 1030;
}
